import React  from "react"
import Layout from "../../components/layout"
import SEO from '../../components/seo'
import DeleteJobPost from '../../components/DeleteJobPost/index'
import stl from '../../STYLES/USER_INFO/user_info.module.scss'
import moment from 'moment';
import { navigate , Link } from 'gatsby' 
import { withUnAuth } from '../../components/ifAuth'
import headerStyle from '../../STYLES/HEADER/header-nobg.module.scss';

const ProfileBody = withUnAuth(({ getCurrentUser }) => {

    const {username , name , comments, jobPosts , aboutMe , age , characteristic, email, peopleVoted, score , phone_number, location , skills , joinDate , job} = getCurrentUser
    return (
          <div className = {stl.background}>
     <div className = {stl.wrapper}>
        <h1>PROFILE</h1>
        <div className = {stl.infoWrapper}>
          <h3>My Info</h3>
                <h4>Username:</h4>
                <span> {username}</span>
                <h4>Name:</h4>
                <span> {name ? name : '-'}</span>
                <h4>Joined Date:</h4>
                <span> {moment(Number(joinDate)).format('MMMM Do YYYY')}</span>
                <h4>Nationality:</h4>
                <span> {characteristic.nationality ? characteristic.nationality : '-'}</span>
                <h4>Age: </h4>
                <span> {age ? age : '-'}</span>
                <h4>Languages Speak: </h4>
                <span>{characteristic.languages.length ? characteristic.languages.map(lang => lang + '  ') : '-'}</span>
                <h4>About me: </h4>
                <span>{aboutMe ? aboutMe : '-'}</span>
                <h4>Skills \ Experience:</h4>
                <span> {skills ? skills : '-'}</span>
          <h3>My Contacts</h3>
                <h4>Phone:</h4>
                <span> {phone_number ? phone_number : "-"}</span>
                <h4>Email:</h4>
                <span>{email ? email : "-"}</span>
          <h3>My Location</h3>
                <h4>State:</h4>
                <span>{location.state ? location.state : '-'}</span>
                <h4>City:</h4>
                <span> {location.city ? location.city : '-'}</span>
          <h3>Availability</h3>
                <h4>I hire employees:</h4>
                <span> {job.hiring ? 'yes' : 'no'}</span>
                <h4>I am hirable: </h4>
                <span>{job.working ? 'yes' : 'no'}</span>
                <h4>My profile searchable for Employers:</h4>
                <span>{job.available ? 'yes' : 'no'}</span>
         <h3>Rating</h3>
         <h4>People Rated Me: </h4>
                <span>{peopleVoted.length}</span>
         <h4>My Rating: </h4>
                <span>{peopleVoted.length? (score.reduce((acc,val) => acc += val , 0 ) / 2).toFixed(1) + ' of  5' : 'No one yet rated you'}</span>
        
        <h3>My Job Posts</h3>
        <span className = {stl.jobPosts}>
              {jobPosts.length ?
              <ul >
                    {jobPosts.map(({title , _id , createDate}) => <li key = {_id}>
                     <div>
                          <h5 style = {{wordBreak:"break-word"}} >{title}</h5>
                          <p>{moment(Number(createDate)).format('MMMM Do YYYY')}</p>
                     </div>
                    <DeleteJobPost  userId = {getCurrentUser._id}  _id = {_id} />
                    </li>)}
              </ul>
              :"-"}
        </span>
        <h3>People's comments</h3>
        <ul className = {stl.comments}>
        {comments.map(({comment , from , _id}) => (
          <li key = {_id}>
            <h4><Link to = {`/user/#/${from.username}`}>{from.username}</Link></h4>
            <span >{comment}</span>
          </li>
        ))}
        </ul>
        </div>
        <button onClick = {() => navigate('/profile/edit')}>Edit Profile</button>
     </div>
     </div>
    )
})

const Profile = () => (
    <Layout headerStyle = {headerStyle} lang = 'en' >
        <SEO title = 'profile' />
        <ProfileBody />
    </Layout>
)

export default Profile