import React  from "react"
import Close from '../../assets/close.svg'
import { useMutation} from '@apollo/react-hooks';
import { DELETE_JOB_POST , GET_CURRENT_USER , GET_ALL_JOB_ADDS } from '../../queries/index'
import {  ErrorAlert } from '../../helpers/index'
import stl from '../../STYLES/MY_PROFILE/profile.module.scss' 

const DeleteJobPost = ({ _id , userId  }) => {


const [deleteJobPost , attrs] = useMutation(DELETE_JOB_POST , {
    variables:{
        _id,
        userId
    },
    onError:(error) => ErrorAlert(error),
    refetchQueries:() => [{query:GET_ALL_JOB_ADDS} , {query:GET_CURRENT_USER}],
})
    return(
        <span className = {stl.cross} onClick = {() => deleteJobPost()} >
           {attrs.loading ? <p>deleting...</p> : <Close  />}
        </span>
    )
}

export default DeleteJobPost